
.item-list {
   display: flex;
	flex-wrap: wrap;
	flex-direction: column;
	max-width: 100%;
	margin: 20px;
   padding: 10px;
}

.item {
	list-style: none;
   width: 250px;
	padding: 15px;
	display: flex;
   align-items: center;
	font-size: 20px; 
	border: 1px solid black;
	border-top: none;
}

.item:first-child {
	border-top: 1px solid black;
}

.item:hover {
	cursor: pointer;
	background-color: #fff99f;
}